.chat__message-server {
  display: block;
  position: relative;
  border-radius: 10px;
  background-color: #293038;
  padding: 0.5em;
  inline-size: fit-content;
  max-inline-size: 75%;
  overflow: hidden;
  border-left: 3px solid #7abcff;
  font-size: inherit;
}

@media (width <= 1024px) {
  .chat__message-server {
    max-inline-size: 80%;
  }
}
