.App {
  display: grid;
  grid-template-columns: min(25%, 400px) 1fr;
  block-size: 100%;
  color: #cdd2d8;
  font-size: 16px;
  font-variation-settings: "wght" 500;
}

.chat-threads__show-button {
  display: none;
  position: fixed;
  transition: box-shadow 200ms ease-in-out, border-color 200ms ease-in-out;
  inset: 13px 0.5rem;
  border: 3px solid transparent;
  border-radius: 50%;

  aspect-ratio: 1;
  inline-size: 40px;
  z-index: 10;

  &:active {
    box-shadow: 0 0 0 4px rgba(94, 167, 222, 0.224);
    border-color: #7abcff;
  }

  &::before,
  &::after {
    display: block;
    position: absolute;
    background-color: #cdd2d8;
    block-size: 1.5px;
    content: "";
  }
  &::before { inset: 13px 8px; inline-size: 18px; }
  &::after { inset: 19px 8px; inline-size: 14px; }
}

@media (width <= 1024px) {
  .App {
    grid-template-columns: 1fr;
  }

  .chat-threads__show-button {
    display: block;
  }
}
