.chat-threads__container {

  transform: translateX(-100%);
  transition: transform 200ms ease-in-out;
  block-size: 100dvh;

  &.active {
    transform: translateX(0);
  }
}

.chat-threads__content {
  transition: transform 200ms ease-in-out;
  background-color: #1d2229;
  padding: 1em;
  inline-size: 100%;
  block-size: 100%;

  & > h2 {
    margin-bottom: 1rem;
    text-align: center;
  }
}

.filter-blur {
  display: none;
  position: absolute;
  left: 80%;
  z-index: 9;
  backdrop-filter: blur(3px);
  inline-size: 20vw;
  block-size: 100vh;
}

@media (width <= 1024px) {
  .chat-threads__container {
    display: block;
    position: fixed;
    z-index: 10;
    inset: 0 0;
    inline-size: 100vw;
  }

  .chat-threads__content {
    inline-size: 80vw;
  }

  .filter-blur {
    display: block;
  }
}
