.main-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  justify-self: center;
  padding: 1em;
  inline-size: 100%;
  max-inline-size: 1024px;
  block-size: 100dvh;

  & > h1 {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 1rem;
    min-block-size: 48px;
    text-align: center;
  }
}

.chat-wrapper {
  display: flex;
  position: relative;
  flex-grow: 1;
  flex-direction: column;
  gap: 1.5em;
  padding: 0.5em;
  max-block-size: 50vh;
  overflow-y: auto;
  font-size: 16px;

  & > * {
    border: 1px solid transparent;
  }
}

.chat__message-wrapper {
  display: flex;
}

@media (width <= 1024px) {
  .main-container {
    justify-content: space-between;
    padding: 0.5em;

    & > h1 {
      margin-bottom: 0;
      font-size: 18px;
      font-variation-settings: "wght" 600;
      // &::after {
      //   display: block;
      //   position: absolute;
      //   z-index: 8;
      //   inline-size: 100dvw;
      //   block-size: 1px;
      //   background-color: #3d454f;
      //   inset: 0;
      //   content: "";
      // }
    }
  }

  .chat-wrapper {
    gap: 0.5em;
    padding: 0;
    max-block-size: none;
    font-size: 18px;
  }
}
